.history-div {
    margin-top: 100px;
    margin-left: 300px;
    margin-right: 300px;
    margin-bottom: 100px;
    background-color: #f9f9f9;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    font-size: 20px;
}

.history-title {
    display: flex;
}
.history-text {
    margin-bottom: 30px;
}



.history-img-slider-img {
    object-fit: contain;
    width: 100%;
    height: 100%;
    display: block;
}

.history-img-button {
    background-color: rgb(15, 15, 15);
    color: white;
    border: none;
    padding: 10px 20px;
    margin: 20px;
    cursor: pointer;
    font-size: 16px;
}

.history-img-button:hover {
    background-color: rgb(102, 2, 2);
    transition: 0.3s;
}

.history-image-slider-div {
    max-width: 1200px;
    max-width: 100%;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    margin-bottom: 100px;
    background-color: #dddddd;
    border-radius: 20px;
}

.gallery {
  border: 1px solid #201f1f;
  margin: 10px;
  background-color: white;
  border-radius: 10px;
}

.history-image-container {
  width: 400px;
  height: 300px;
}

.history-gallery-image{
  object-fit: cover;
  width: 100%;
  height: 100%;
  border-radius: 10px;
}
  
.gallery:hover {
  border: 1px solid #777;
}


  
  
  
@media screen and (max-width: 500px) {
    .history-div {
        margin: 10px;
        font-size: 20px;
    }
    .history-image-slider-div {
        height: 300px;
    }
}